html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  background-color: #ebe7de;
}

p {
  color: #000000;
  font-size: 1.6rem;
  line-height: 2;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
  color: #000000;
}

img {
  max-width: 100%;
  display: block;
  height: auto;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

h1,
h2,
h3 {
  font-family: "Raleway", sans-serif;
  margin: 0 0 5rem 0;
  text-transform: uppercase;
  text-align: center;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
  color: #5e311e;
}
h2 span {
  color: #c39c63;
  font-size: 1.8rem;
  display: block;
}
h2::after {
  content: "";
  display: block;
  width: 10rem;
  height: 10rem;
  margin: 0 auto;
  background-image: url(../img/cafe.svg);
  background-size: 10rem;
}

h3 {
  font-size: 3.2rem;
}

.header {
  padding: 5rem 0;
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;
}
@media (min-width: 768px) {
  .header {
    min-height: 70rem;
  }
}
.header::before {
  content: "";
  background-color: #000000;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0.6;
}
.header__container {
  position: relative;
}
.header__bg {
  background-image: url(../img/header_inicio.jpg);
}
.header__bg--about-us {
  background-image: url(../img/header_nosotros.jpg);
}
.header__bg--process {
  background-image: url(../img/header_proceso.jpg);
}
.header__bg--menu {
  background-image: url(../img/header_menu.jpg);
}
.header__bg--gallery {
  background-image: url(../img/header_galeria.jpg);
}
.header__bg--contact {
  background-image: url(../img/header_contacto.jpg);
}
.header__grid {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .header__grid {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
.header__logo {
  width: 50%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .header__logo {
    width: 20rem;
    margin: 0;
  }
}
.header__heading {
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .header__heading {
    margin-top: 10rem;
  }
}

.navbar {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .navbar {
    flex-direction: row;
    gap: 1rem;
    margin-top: 0;
  }
}
.navbar__link {
  color: #ffffff;
  font-size: 2rem;
  text-transform: uppercase;
  font-weight: 700;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .navbar__link {
    margin-bottom: 0;
  }
}
.navbar__link:hover {
  color: #c39c63;
}
.navbar__link--active {
  color: #c39c63;
}

.main-content {
  background-color: #ffffff;
  padding: 5rem 2rem;
}
@media (min-width: 768px) {
  .main-content {
    padding: 10rem;
    margin-top: -10rem;
    position: relative;
  }
}

.icons {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .icons__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.icons__img {
  margin: 0 auto;
  width: 12rem;
}
.icons__name {
  font-family: "Bona nova", serif;
  font-size: 3.2rem;
  color: #5e311e;
}

.gallery__grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 1024px) {
  .gallery__grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
.gallery__img img {
  height: 30rem;
  width: 30rem;
  object-fit: cover;
}
@media (min-width: 1024px) {
  .gallery__img--h62 img {
    height: 62rem;
  }
  .gallery__img--w62 img {
    width: 62rem;
  }
  .gallery__img--w94 img {
    width: 94rem;
  }
  .gallery__img:nth-child(1) {
    grid-row: 1/3;
    grid-column: 1/2;
  }
  .gallery__img:nth-child(2) {
    grid-column: 2/4;
    grid-row: 1/2;
  }
  .gallery__img:nth-child(4) {
    grid-column: 2/3;
    grid-row: 2/4;
  }
  .gallery__img:nth-child(5) {
    grid-column: 3/5;
    grid-row: 2/3;
  }
  .gallery__img:nth-child(6) {
    grid-column: 3/5;
    grid-row: 3/4;
  }
  .gallery__img:nth-child(7) {
    grid-column: 1/2;
    grid-row: 3/5;
  }
  .gallery__img:nth-child(8) {
    grid-column: 2/5;
    grid-row: 4/5;
  }
  .gallery__img:nth-child(9) {
    grid-column: 1/3;
    grid-row: 5/6;
  }
  .gallery__img:nth-child(10) {
    grid-column: 3/5;
    grid-row: 5/6;
  }
}

.menu {
  background-image: url(../img/menu_arriba.svg), url(../img/menu_abajo.svg), linear-gradient(to top, #5e311e, #5e311e);
  background-repeat: no-repeat;
  background-position: center 5rem, center 92%, 100%;
  background-size: 200%;
  padding: 15rem 2rem 20rem 2rem;
}
@media (min-width: 768px) {
  .menu {
    padding: 15rem 0 20rem 0;
    background-size: 100%;
  }
}
@media (min-width: 1024px) {
  .menu {
    background-size: 50%, 50%, 100%;
  }
}
.menu__heading, .menu__heading span {
  color: #ffffff;
}
.menu__heading::after, .menu__heading span::after {
  display: none;
}
@media (min-width: 768px) {
  .menu__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
.menu__title {
  color: #ffffff;
}
.menu__item {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.menu__item p {
  color: #ffffff;
  font-size: 2.2rem;
  line-height: 1.5;
  margin: 0 0 2rem 0;
}
.menu__item p span {
  color: #c39c63;
  display: block;
}
.menu__item .menu__price {
  font-size: 4rem;
  font-weight: 700;
}

.testimonials {
  padding: 10rem 2rem;
}
@media (min-width: 768px) {
  .testimonials {
    padding: 10rem;
  }
}
.testimonials__testimony {
  background-color: #ebe7de;
  max-width: 50rem;
  margin: 0 auto;
  padding: 2rem 4rem;
  border-radius: 1rem;
}
.testimonials__autor {
  font-family: "Bona nova", serif;
  font-size: 2.4rem;
  color: #5e311e;
  text-align: right;
}

.footer {
  background-color: #5e311e;
}
.footer__grid {
  padding: 5rem;
}
@media (min-width: 768px) {
  .footer__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.footer__box {
  font-family: "Bona nova", serif;
  text-align: center;
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .footer__box {
    margin-bottom: 0;
  }
}
.footer__heading {
  font-family: "Bona nova", serif;
  margin: 0 0 1rem 0;
  font-size: 2.4rem;
  color: #c39c63;
}
.footer__info {
  font-size: 2rem;
  margin: 0;
}
.footer__copyright {
  text-align: center;
  margin: 0;
  color: #ffffff;
  padding: 2rem;
}

@media (min-width: 768px) {
  .about-us {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
.about-us__img {
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .about-us__img {
    margin: 0 2rem 0 0;
  }
}
.about-us__img img {
  transform: rotate(-6deg);
  border: 3rem solid #ffffff;
  box-shadow: 9px 10px 5px -7px rgba(0, 0, 0, 0.35);
}
.about-us__txt blockquote {
  margin: 0;
  padding: 1rem;
  border-left: 0.5rem solid #5e311e;
  font-size: 2rem;
}

.process {
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .process {
    display: flex;
    gap: 5rem;
    align-items: center;
  }
  .process:last-of-type {
    margin-bottom: 0;
  }
  .process:nth-child(even) {
    flex-direction: row-reverse;
  }
}
.process__txt, .process__img {
  flex: 1;
}
.process__txt {
  margin-top: 1rem;
}
.process__heading {
  margin: 0;
  text-align: left;
  color: #5e311e;
}
.process__heading::after {
  content: "";
  display: block;
  width: 50%;
  height: 5px;
  background-color: #c39c63;
}

.ingredients__content {
  background-image: url(../img/cafe.jpg);
  background-repeat: no-repeat;
  background-position: center bottom;
  background-size: 100%;
  aspect-ratio: 3/6;
}
@media (min-width: 480px) {
  .ingredients__content {
    aspect-ratio: 3/4;
  }
}
@media (min-width: 768px) {
  .ingredients__content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
    aspect-ratio: 16/6;
    background-size: 40%;
    background-position: bottom right;
  }
}
.ingredients__txt p {
  font-size: 2rem;
}

@media (min-width: 768px) {
  .contact {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
    background-image: url(../img/granos_cafe.jpg);
    background-repeat: no-repeat;
    background-position: right center;
    background-size: 40%;
  }
}
.contact__field {
  margin-bottom: 2rem;
}
.contact__field:last-of-type {
  margin-bottom: 0;
}
.contact__field label {
  text-transform: uppercase;
  display: block;
  font-size: 2.2rem;
  font-weight: 700;
}
.contact__field input:not([type=submit]),
.contact__field select {
  width: 100%;
  padding: 1rem;
  border: 0.5rem solid #5e311e;
}
.contact__field input[type=submit] {
  background-color: #5e311e;
  width: 100%;
  border: none;
  padding: 1rem;
  color: #ffffff;
  font-size: 2.2rem;
  font-weight: 700;
  text-transform: uppercase;
}
.contact__field input[type=submit]:hover {
  cursor: pointer;
}

.txt-center {
  text-align: center;
}

.bg-white {
  background-color: #ffffff;
}

.mt-n10 {
  margin-top: -10rem;
}

.pb-0 {
  padding-bottom: 0;
}